export function getDecorIdeaBySlug(slug: string): DecorIdea | undefined {
  return decorIdeas.find((idea) => idea.slug === slug)
}

export function getAllDecorSlugs(): string[] {
  return decorIdeas.map((idea) => idea.slug)
}

